@font-face {
  font-family: 'Inter-Variable';
  src: local('Inter-Variable'),
  url(./fonts/Inter-Variable.woff2) format('woff2 supports variations'),
  url(./fonts/Inter-Variable.woff2) format('woff2-variations');
  font-weight: 400 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'),
  url(./fonts/Montserrat-VariableFont_wght.ttf) format('truetype-variations');
  font-weight: 400 900;
  font-display: swap;
}